<template>
    <div class="add-port" v-loading="portLoading">
        <div class="title" v-if="panelType == 'add'">
            添加端口
        </div>
        <div class="title" v-if="panelType == 'edit'">
            编辑端口
        </div>
        <div class="form">
            <el-form :model="addPortData" label-width="100px" :label-position="`top`" ref="portForm">
                <el-row :gutter="20">
                    <el-col :span="7">
                        <el-form-item 
                            label="端口号" 
                            prop="port" 
                            :rules="[
                                { required: true, message: '端口不能为空'},
                                { type: 'number', message: '端口必须为数字'}
                            ]"
                        >
                            <el-input :disabled="panelType == 'edit'" placeholder="请输入端口" v-model.number="addPortData.port" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="7">
                        <el-form-item 
                            label="挖矿币种" 
                            prop="coin" 
                            :rules="[
                                { required: true, message: '端口不能为空'},
                            ]"
                        >
                            <el-select v-model="addPortData.coin" placeholder="选择币种" @change="changeCoin">
                                <el-option label="ETH" value="ETH"></el-option>
                                <el-option label="BTC" value="BTC"></el-option>
                                <el-option label="ETC" value="ETC"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item 
                            label="最大连接数" 
                        >
                            <el-input placeholder="默认0, 无上限" v-model.number="addPortData.maxConnect" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item 
                            label="开启SSL" 
                        >
                            <el-switch :disabled="panelType == 'edit'" v-model="addPortData.ssl"></el-switch>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col v-if="!addPortData.customPoolURL" :span="20">
                        <el-form-item 
                            label="目标矿池" 
                            prop="poolURL" 
                            :rules="[
                                { required: true, message: '目标矿池不能为空'},
                            ]"
                        >  
                            <el-select v-model="addPortData.poolURL" placeholder="请选择" style="width:100%;"> 
                                <el-option-group
                                    v-for="pool in addPortData.pools"
                                    :key="pool.label"
                                    :label="pool.label"
                                >
                                    <el-option
                                        v-for="item in pool.options"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-option-group>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col v-if="addPortData.customPoolURL" :span="20">
                        <el-form-item 
                            label="目标矿池" 
                            prop="poolURL" 
                            :rules="[
                                { required: true, message: '矿池地址不能为空'}
                            ]"
                        >
                            <el-input placeholder="请输入目标矿池地址" v-model="addPortData.poolURL" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item 
                            label="自定义矿池" 
                        >
                            <el-switch v-model="addPortData.customPoolURL"></el-switch>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col v-if="!addPortData.customSparePoolURL" :span="20">
                        <el-form-item 
                            label="备用矿池" 
                        >  
                            <el-select v-model="addPortData.sparePoolURL" placeholder="请选择" style="width:100%;"> 
                                <el-option-group
                                    v-for="pool in addPortData.pools"
                                    :key="pool.label"
                                    :label="pool.label"
                                >
                                    <el-option
                                        v-for="item in pool.options"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-option-group>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col v-if="addPortData.customSparePoolURL" :span="20">
                        <el-form-item 
                            label="备用矿池" 
                            prop="sparePoolURL" 
                        >
                            <el-input placeholder="请输入目标矿池地址" v-model="addPortData.sparePoolURL" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item 
                            label="自定义矿池" 
                        >
                            <el-switch v-model="addPortData.customSparePoolURL"></el-switch>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div class="hr" style="margin-bottom:0;"></div>
            <el-form :model="addPortData" label-width="100px" :label-position="`left`">
                <el-collapse v-model="commissionPanel" @change="commissionHandle">
                    <el-collapse-item name="1">
                        <template slot="title">
                            <el-row :gutter="20">
                                <el-col :span="4">
                                    <el-form-item 
                                        label="开启抽水" 
                                    >
                                            <el-switch v-model="addPortData.isCommission"></el-switch>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                        </template>
                        <!-- <div class="hr"></div> -->
                        <el-row>
                            <el-col :span="24">
                                <el-form-item 
                                    label="抽水比例" 
                                >
                                    <el-slider
                                        :step="0.1"
                                        :min="0"
                                        :max="100"
                                        v-model="addPortData.commission"
                                        show-input
                                        :format-tooltip="formatCommission"
                                    >
                                    </el-slider>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row :gutter="20">
                            <el-col :span="24">
                                <el-form-item 
                                    label="抽水钱包" 
                                >
                                    <el-input placeholder="请输入钱包地址" v-model="addPortData.commissionWallet" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row :gutter="20">
                            <el-col :span="24">
                                <el-form-item 
                                    label="抽水矿池"
                                >
                                    <el-input placeholder="请输入抽水矿池地址" v-model="addPortData.commissionPool" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row :gutter="20">
                            <el-col :span="20">
                                <el-form-item 
                                    label="统一钱包" 
                                >
                                    <el-input :disabled="!addPortData.openUnified" placeholder="请输入钱包地址" v-model="addPortData.unified" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="4">
                                <el-form-item class="right-radio">
                                    <el-tooltip class="item" effect="dark" content="拦截所有设备的钱包地址并替换。" placement="top">
                                        <el-switch v-model="addPortData.openUnified"></el-switch>
                                    </el-tooltip>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-row :gutter="20">
                            <el-col :span="12">
                                <el-form-item 
                                    label="矿池模式" 
                                >
                                    <el-tooltip class="item" effect="dark" content="需要开启【统一钱包】功能，打开矿池模式后，此端口下的所有设备将合并为一台设备，可降低数据提交延迟，设备数量越多越明显。" placement="top">
                                        <el-switch :disabled="!addPortData.openUnified" v-model="addPortData.poolMode"></el-switch>
                                    </el-tooltip>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item 
                                    label="抽水拦截" 
                                >
                                    <el-tooltip class="item" effect="dark" content="需要开启【统一钱包】功能，打开抽水拦截后，所有的内核抽水及设备本地抽水份额会返至统一钱包地址。" placement="top">
                                        <el-switch :disabled="!addPortData.openUnified" v-model="addPortData.intercept"></el-switch>
                                    </el-tooltip>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-collapse-item>
                </el-collapse>
                <div class="hr"></div>
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="备注">
                            <el-input type="textarea" v-model="addPortData.desc"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-form label-width="100px" :label-position="`left`">
                <el-row>
                    <el-col :span="24">
                        <el-form-item>
                            <el-button v-if="panelType == 'add'" size="small" type="primary" @click="submitCreate">立即创建</el-button>
                            <el-button v-if="panelType == 'edit'" size="small" type="primary" @click="submitEdit">保存</el-button>
                            <el-button size="small" @click="onCancel">取消</el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
        </div>
    </div>
</template>

<script>
    import { mapState, mapActions, mapGetters } from 'vuex'

    export default {
        computed:{
            // ...mapGetters('port', {
            //     getPortType: 'getPortType',
            //     getOnlay: 'getOnlay'
            // })
        },
        mounted() {
            this.updatePools();
        },
        data() {
            return {
                commissionPanel: [],
                portLoading: true,
                panelType: 'add',
                addPortData: {
                    port: '',   // 端口号
                    coin: 'ETH',   // 挖矿币种
                    ssl: true,    // 是否开启ssl
                    maxConnect: '',     // 最大连接数
                    poolURL: '',        // 目标矿池
                    ProxyIsTls: false,  // 目标矿池tls
                    sparePoolURL: '',   // 备用矿池
                    StandbyProxyIsTls: false,   // 备用矿池tls
                    customPoolURL: false,       // 是否开启自定义矿池
                    customSparePoolURL: false,  // 是否开启备用自定义矿池
                    isCommission: false,    // 是否开启抽水
                    commission: 0,          // 抽水比例
                    commissionWallet: '',   // 抽水钱包地址
                    commissionPool: '',     // 抽水矿池地址
                    PumpingProxyIsTls: false,   // 抽水矿池tls
                    openUnified: false,     // 是否开启统一钱包地址,
                    unified: '',            // 统一钱包地址
                    poolMode: false,         // 矿池模式
                    desc: '',   // 备注
                    intercept: false,   // 抽水拦截
                    pools: []
                }
            }
        },
        methods: {
            ...mapActions('port', [
                'updateTotalServer'
            ]),
            commissionHandle() {
                if (this.addPortData.isCommission) {
                    this.commissionPanel = ['1'];
                } else {
                    this.commissionPanel = [];
                }
            },
            // 选择币种切换时, 更新预置对应币种的矿池列表
            updatePools() {
                if (this.addPortData.coin && this.$PRESET_POOL[this.addPortData.coin]) {
                    this.addPortData.pools = JSON.parse(JSON.stringify(this.$PRESET_POOL[this.addPortData.coin]));
                    this.addPortData.poolURL = '';
                } else {
                    this.addPortData.pools = [];
                    this.addPortData.poolURL = '';
                }
            },
            changeCoin() {
                this.updatePools();
            },
            submitCreate() {
                let addPortData = this.addPortData;
                let ref = this.$refs['portForm'];
                
                ref.validate(valid => {
                    if (valid) {
                        this.axios({
                            method: 'post',
                            url: `${this.$API.createPort}`,
                            data: {
                                ProxyAddr: addPortData.poolURL,
                                LocalAddr: `0.0.0.0:${addPortData.port}`,
                                localIsTls: addPortData.ssl,
                                ProxyIsTls: addPortData.ProxyIsTls,
                                Type: addPortData.coin,
                                PumpingRate: this.$Func.transCommission(addPortData.commission).effect,
                                PumpingRateLimit: this.$Func.transCommission(addPortData.commission).totalEffect,
                                Wallet: addPortData.commissionWallet,
                                UnifiedWallet: addPortData.unified,
                                StandbyProxyAddr: addPortData.sparePoolURL,
                                PumpingProxyAddr: addPortData.commissionPool,
                                StandbyProxyIsTls: addPortData.StandbyProxyIsTls,
                                PumpingProxyIsTls: addPortData.PumpingProxyIsTls,
                                IsPumping: addPortData.isCommission,
                                MaxConnect: addPortData.maxConnect ? addPortData.maxConnect : 0,
                                Name: addPortData.desc,
                                IsMiningPoolMode: addPortData.poolMode,
                                IsUnifiedWallet: addPortData.openUnified,
                                IsCustomProxyAddr: addPortData.customPoolURL,
                                IsCustomStandbyAddr: addPortData.customSparePoolURL
                            }
                        }).then(res => {
                            if (res.status == 200) {
                                this.$message.success('创建成功');

                                this.updateTotalServer();
                                this.onCancel();
                            } else {
                                this.$message.warning(`创建失败, 错误代码: ${res.status}`);
                            }
                        }).catch(err => {
                            this.$message.error(err);
                        })
                    } else {
                        return false;
                    }
                });
            },
            submitEdit() {
                let addPortData = this.addPortData;
                
                this.axios({
                    method: 'put',
                    url: `${this.$API.createPort}/${addPortData.port}`,
                    data: {
                        ProxyAddr: addPortData.poolURL,
                        ProxyIsTls: addPortData.ProxyIsTls,
                        Type: addPortData.coin,
                        PumpingRate: this.$Func.transCommission(addPortData.commission).effect,
                        PumpingRateLimit: this.$Func.transCommission(addPortData.commission).totalEffect,
                        Wallet: addPortData.commissionWallet,
                        UnifiedWallet: addPortData.unified,
                        StandbyProxyAddr: addPortData.sparePoolURL,
                        PumpingProxyAddr: addPortData.commissionPool,
                        StandbyProxyIsTls: addPortData.StandbyProxyIsTls,
                        PumpingProxyIsTls: addPortData.PumpingProxyIsTls,
                        IsPumping: addPortData.isCommission,
                        MaxConnect: addPortData.maxConnect ? addPortData.maxConnect : 0,
                        Name: addPortData.desc,
                        IsMiningPoolMode: addPortData.poolMode,
                        IsUnifiedWallet: addPortData.openUnified,
                        IsCustomProxyAddr: addPortData.customPoolURL,
                        IsCustomStandbyAddr: addPortData.customSparePoolURL
                    }
                }).then(res => {
                    if (res.status == 200) {
                        this.$message.success('修改成功');

                        this.updateTotalServer();
                        this.onCancel();
                    } else {
                        this.$message.warning(`修改失败, 错误代码: ${res.status}`);
                    }
                }).catch(err => {
                    this.$message.error(err);
                })
            },
            clean() {
                // this.addPortData.port = '';
                // this.addPortData.coin = '';
                // this.addPortData.ssl = '';
                // this.addPortData.maxConnect = '';
                // this.addPortData.poolURL = '';
                // this.addPortData.sparePoolURL = '';
                // this.addPortData.customPoolURL = false;
                // this.addPortData.customSparePoolURL = false;
                // this.addPortData.commission = 0;
                // this.addPortData.commissionWallet = '';
                // this.addPortData.commissionPool = '';
                // this.addPortData.openUnified = false;
                // this.addPortData.unified = '';
                // this.addPortData.poolMode = false;
            },
            requestEditData(port) {
                let requestURL = this.$API.getPort + '/' + port;

                this.axios.get(requestURL).then(res => {
                    if (res.status == 200) {
                        this.setEditData(res.data.Server)
                    } else {
                        this.$message.error( `ERROR: ${res.error}`);   
                    }

                    this.portLoading = false;
                }).catch(err => {
                    this.$message.error(err);
                    console.error(err);

                    this.portLoading = false;
                })
            },
            setEditData(data) {
                this.addPortData.port = Number(data.LocalAddr.split(':')[1]);
                this.addPortData.coin = data.Type;
                this.changeCoin();
                this.addPortData.commission = this.$Func.transPercent(data.PumpingRate, data.PumpingRateLimit),
                this.addPortData.maxConnect = data.MaxConnect ? data.MaxConnect : '';
                this.addPortData.ssl = data.LocalIsTls;
                this.addPortData.poolURL = data.ProxyAddr;
                this.addPortData.sparePoolURL = data.StandbyProxyAddr;
                this.addPortData.commissionWallet = data.Wallet;
                this.addPortData.commissionPool = data.PumpingProxyAddr;
                this.addPortData.isCommission = data.IsPumping;
                this.commissionHandle();
                this.addPortData.desc = data.Name;
                this.addPortData.unified = data.UnifiedWallet;
                this.addPortData.openUnified = data.IsUnifiedWallet;
                this.addPortData.customPoolURL = data.IsCustomProxyAddr;
                this.addPortData.customSparePoolURL = data.IsCustomStandbyAddr;
                this.addPortData.poolMode = data.IsMiningPoolMode;
            },
            setPanelType(type) {
                this.panelType = type;

                if (type == 'add') {
                    this.portLoading = false;
                }
            },
            formatCommission(val) {
                return `${val}%`
            },
            onCancel() {
                this.$modal.hide('add-port')
            }
        },
        watch: {
            'addPortData.openUnified' (val) {
                if (!val) {
                    this.addPortData.unified = '';
                    this.addPortData.poolMode = false;
                    this.addPortData.intercept = false;
                }
            }
        }
    }
</script>

<style lang="scss">
    .add-port {
        padding: 20px;

        .title {
            color: $fontColor2;
            margin-bottom: 20px;
        }

        .hr{
            background: #3a4250;
            width: 100%;
            height: 1px;
            margin-bottom: 20px;
        }

        .right-radio .el-form-item__content{
            margin-left: 0!important;
        }

        .el-loading-mask{
            background-color: rgba(0,0,0,.5);
        }

        .el-icon-question{
            font-size: 20px;
            display: inline-flex;
            align-items: center;
            vertical-align: middle;
        }
    }
</style>